@layer objects {
    .o-flex {
        display: flex;

        &--wrap {
            flex-wrap: wrap;
        }

        &--jc-sb {
            justify-content: space-between;
        }

        &--jc-e {
            justify-content: flex-end;
        }

        &--jc-c {
            justify-content: center;
        }

        &--ai-c {
            align-items: center;
        }

        &--ai-e {
            align-items: flex-end;
        }

        &--ai-b {
            align-items: baseline;
        }

        &--equal > * {
            flex: 1 1 0;
        }

        &--column {
            flex-direction: column;
        }
    }

    .o-flex-item {
        &--shrink-0 {
            flex-shrink: 0;
        }

        &--shrink-1 {
            flex-shrink: 1;
        }

        &--grow-1 {
            flex-grow: 1;
        }
    }
}
