@layer settings {
    :root {
        --banner__content--width: 100%;
        --banner--display: block;

        @media (--desktop) {
            --banner__content--width: 50%;
            --banner--display: grid;
        }
    }
}

@layer components {
    we-banner {
        --btn--wrapper--jc: center;

        .we-banner__wrapper {
            display: var(--banner--display);
            grid-template-areas: "media";
            place-items: stretch;
            min-height: var(--banner--mh, auto);
        }

        [data-media="video"] {
            grid-area: media;
            position: relative;
            display: block;
            height: var(--banner-media--height, 60vh);
        }

        .we-banner__media-wrapper {
            grid-area: media;
            position: relative;
            display: block;
            align-self: stretch;

            picture {
                height: 100%;
            }
        }

        .we-banner__content-wrapper {
            grid-area: media;
            z-index: 2;
            max-width: var(--banner-content-wrapper--max-width, var(--wrapper--width));
            width: 100%;
            justify-self: center;
            display: flex;
            align-items: var(--banner__content--ab, center);
            justify-content: var(--banner__content--ai, center);
            text-align: var(--banner__content--text-align, center);
        }

        .we-banner__content {
            display: flex;
            flex-direction: column;
            gap: var(--brand__spacing);
            width: var(--banner__content--width);
            padding-block: var(--banner__content--pb, var(--brand__spacing));
            padding-inline: var(--banner__content--pi, var(--wrapper--spacing));
        }

        &[align="left"] {
            --banner__content--ai: start;
            --banner__content--text-align: start;
            --btn--wrapper--jc: start;
        }

        &[align="right"] {
            --banner__content--ai: end;
            --banner__content--text-align: right;
            --btn--wrapper--jc: end;
        }

        &[align="top"] {
            --banner__content--ab: start;
        }

        &[align="bottom"] {
            --banner__content--ab: end;
        }

        // ... More alignment options
    }
}
