@layer elements {
    :where(picture, img) {
        width: 100%;
        height: auto;
    }

    :where(figcaption) {
        font-style: italic;
        opacity: 0.8;
        margin-top: 4px;
        font-size: var(--brand__font-size--small);
        text-align: start;
    }

    :where(svg) {
        fill: currentColor;
        width: var(--svg-size, 1em);
        height: var(--svg-size, 1em);
        flex-shrink: 0;
    }
}
