// Add this class to elements that should only be discoverable by screen readers, eg. labels for inputs
@layer utilities {
    .u-sr {
        position: absolute;
        margin: -1px;
        border: 0;
        padding: 0;
        width: 1px;
        height: 1px;
        overflow: hidden;
        white-space: nowrap;
        clip: rect(0 0 0 0);
        clip-path: inset(50%);
    }
}
