@forward "../objects/objects.wrapper";
@use "../tools/tools" as tools;

:root {
    --quote--color: var(--brand__color--gray-light);
}

@layer components {
    we-quote {
        padding-block: var(--wrapper--spacing);
        container-type: inline-size;

        .we-quote__media {
            grid-area: media;
            border-radius: 999px;
            width: 130px;
            display: flex;
            align-items: center;
            aspect-ratio: 1 / 1;
            overflow: hidden;
        }

        .we-quote__body {
            grid-area: blockquote;
        }

        .we-quote__container {
            display: grid;
            grid-template-columns: auto 1fr;
            grid-template-areas: ". iconStart" "media blockquote";
            grid-template-rows: auto 1fr;
            gap: var(--brand__spacing--tiny) var(--brand__spacing);
            color: var(--quote--color, currentColor);
        }

        .we-quote__start {
            grid-area: iconStart;
            --svg-size: 30px;
        }

        blockquote {
            margin: 0 0 var(--brand__spacing--small) 0;
            font-size: clamp(tools.rem(25px), 2vw + 1rem, tools.rem(35px));
            line-height: 1.2;
            font-weight: 300;
            text-wrap: balance;
        }

        figcaption {
            font-weight: var(--brand__font-weight--bold, bold);
            font-style: normal;
        }

        cite {
            font-style: inherit;
        }

        @container (width < 800px) {
            .we-quote__container {
                grid-template-columns: 1fr;
                grid-template-areas: "media" "iconStart" "blockquote";
                grid-template-rows: auto auto 1fr;
            }

            .we-quote__media {
                margin-block-end: var(--brand__spacing--small);
            }
        }
    }
}
