@forward "../../atoms/atoms.skip-link";

@forward "./components.nav";
@forward "./components.submenu";
@forward "./components.doormat";
@forward "./components.profile";

@layer settings {
    :root {
        --primary-nav--height-full-screen: 74px;
        --primary-nav--height: auto;
        --secondary-nav--height-full-screen: 50px;
        --secondary-nav--height: auto;
        --header--height: var(--primary-nav--height-full-screen) + var(--secondary-nav--height-full-screen);
        --mobile-cta--gap: 2px;

        @media (--desktop) {
            --primary-nav--height-full-screen: 70px;
        }
    }
}

@layer components {
    we-header {
        .is-beauflor,
        .is-berryAlloc {
            display: none;
        }

        width: 100%;
        position: relative;
        z-index: 999;
        display: block;
        background-color: var(--brand__color--white);

        &[sticky] {
            position: sticky;
            top: 0;
        }

        header {
            a {
                color: var(--brand__color--white);
            }

            .we-header__wrapper {
                display: grid;
                grid-template-areas: "logo . mobile-cta toggle";
                grid-template-columns: auto 1fr auto auto;
            }

            .we-header__menu,
            .we-header__menu-container {
                display: contents;
            }

            .we-header__toggle {
                display: flex;
                align-items: center;
                justify-content: center;
                grid-area: toggle;
                width: var(--primary-nav--height-full-screen);
                background: var(--brand__color--gray-lighter);
                border: 0;
                margin-inline: var(--mobile-cta--gap) 0;
            }

            .we-header__logo {
                grid-area: logo;
                margin: auto 0;
                padding-block: var(--brand__spacing--small);

                img {
                    height: 50px;
                    width: auto;
                }
            }

            .we-header__nav {
                grid-template-areas:
                    "header header"
                    "panel panel"
                    "profile profile"
                    "language language";
                grid-template-rows: auto 1fr;
            }

            .we-header__mobile-cta {
                grid-area: mobile-cta;
                display: flex;
                justify-content: flex-end;
                color: var(--brand__color--white);
                gap: var(--mobile-cta--gap);
                --set-brand__link--color: var(--brand__color--white);

                @media (--until-desktop) {
                    .c-nav__item {
                        background-color: var(--brand__color--gray-lighter);
                        height: 100%;
                    }
                }
            }

            .we-header__primary {
                height: auto;
                grid-area: primary;
                color: var(--brand__color--white);
                --set-brand__link--color: var(--brand__color--white);
            }

            .we-header__secondary {
                grid-area: secondary;
                color: var(--brand__color--white);
                --set-brand__link--color: var(--brand__color--white);
            }

            .we-header__language {
                grid-area: language;
                color: var(--brand__color--text);
            }

            .we-header__profile {
                gap: 15px;
                color: var(--brand__color--white);
                --set-brand__link--color: var(--brand__color--white);
                > * {
                    height: fit-content;
                }
            }

            .we-header__cta {
                color: var(--brand__color--white);
                position: relative;
                --set-brand__link--color: var(--brand__color--white);
            }

            .we-header__nav > .we-header__cta {
                display: none;
            }

            .we-header__cta-item {
                width: var(--primary-nav--height-full-screen);
            }

            .we-header__panel {
                display: flex;
                flex-direction: column;
                grid-area: panel;
                overflow-y: auto;
            }

            .we-header__profile {
                grid-area: profile;
                display: flex;
                gap: var(--brand__spacing--small);
                justify-content: space-between;
                align-items: center;
            }

            .we-header__cta {
                grid-area: cta;
                display: flex;
                justify-content: flex-end;
            }

            .we-header__mobile {
                padding: var(--brand__spacing--small) var(--brand__spacing);
                width: 100vw;
                display: flex;
                justify-content: space-between;
                grid-area: header;

                .we-header__logo {
                    img {
                        width: auto;
                    }
                }

                .we-header__cta {
                    grid-area: cta;
                }
            }

            @media (--until-desktop) {
                .we-header__primary,
                .we-header__secondary {
                    flex-direction: column;

                    > .we-header__profile,
                    > .we-header__language {
                        display: none;
                    }
                }

                .we-header__secondary {
                    margin-block-end: auto;
                }

                .we-header__language {
                    display: flex;
                    padding: var(--brand__spacing) 0;
                    background-color: var(--brand__color--gray-light);
                    margin-block-start: 2px;
                    color: var(--brand__color--white);

                    svg {
                        transform: rotate(0deg);
                    }
                }

                .we-header__profile {
                    padding: var(--brand__spacing--small) var(--brand__spacing) var(--brand__spacing);
                    background-color: var(--brand__color--gray-light);
                    margin-block-start: var(--brand__spacing--large);
                }
            }

            @media (--desktop) {
                .we-header__wrapper {
                    grid-template-areas:
                        "secondary secondary secondary"
                        "logo primary cta";
                }

                .we-header__primary {
                    height: var(--primary-nav--height-full-screen);

                    &:before {
                        content: "";
                        position: absolute;
                        display: block;
                        left: 0;
                        right: 0;
                        z-index: -1;
                        background: var(--brand__color--primary);
                        height: var(--primary-nav--height-full-screen);
                    }
                }

                .we-header__secondary {
                    height: var(--secondary-nav--height-full-screen);

                    a,
                    span {
                        color: var(--brand__color--text);
                    }

                    &:before {
                        content: "";
                        position: absolute;
                        display: block;
                        left: 0;
                        right: 0;
                        z-index: -1;
                        background: var(--brand__color--gray-lighter);
                        height: var(--secondary-nav--height-full-screen);
                    }
                }

                .we-header__language {
                    height: var(--secondary-nav--height-full-screen);

                    .c-nav__items {
                        padding: 0;
                        margin: 0;
                    }
                }

                .we-header__profile {
                    margin-block-start: initial;
                    margin-inline-end: var(--brand__spacing);
                }

                .we-header__nav > .we-header__cta {
                    width: fit-content;
                    height: var(--primary-nav--height-full-screen);
                }

                .we-header__mobile-cta {
                    display: none;
                }

                .we-header__toggle {
                    display: none;
                }

                .we-header__nav {
                    display: contents;
                }

                .we-header__mobile {
                    display: none;
                }

                .we-header__panel {
                    display: contents;

                    > .we-header__profile,
                    > .we-header__language {
                        display: none;
                    }
                }

                .we-header__nav > .we-header__cta {
                    display: flex;
                }
            }
        }

        @media (--until-desktop) {
            [open] .we-header-panel__content {
                left: 0;
                visibility: visible;

                .we-header__toggle {
                    padding-block: var(--brand__spacing);
                    align-self: flex-start;
                }
            }

            .we-header-panel__content {
                visibility: hidden;
                display: grid;
                position: fixed;
                left: var(--header-panel__content--left, 100%);
                top: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
                transition: left 0.2s ease;
                background-color: var(--brand__color--primary);
            }
        }
    }
}
