@layer settings {
    :root {
        --wrapper--spacing: 20px;
        --wrapper--width: 80rem;
        --wrapper--width--small: 60rem;

        // Use this variable to mimic the inline wrapping content (eg. when you need full background image but content not, cfr. Banner)
        --wrapper--mimic-spacing: max(calc((100vw - var(--wrapper--width)) / 2), var(--wrapper--spacing));

        --wrapper--mimic-spacing--small: max(calc((100vw - var(--wrapper--width--small)) / 2), var(--wrapper--spacing));

        @media (--wide) {
            --wrapper--width--small: 80rem;
        }
    }
}

@layer objects {
    .o-wrapper {
        display: grid;
        column-gap: var(--wrapper--spacing);
        grid-template-columns:
            minmax(0, 1fr)
            min(var(--wrapper--width), calc(100% - 2 * var(--wrapper--spacing)))
            minmax(0, 1fr);

        & > * {
            grid-column: 2;
        }

        &--small {
            --wrapper--width: var(--wrapper--width--small);
        }

        &--full-width {
            grid-column: 1/-1;
        }
    }
}
