// Todo
@layer elements {
    ::placeholder {
        color: var(--brand__color--primary);
    }

    :where(input) {
        height: var(--brand__input--height);
    }

    :where(textarea) {
        width: 100%;
        resize: vertical;
        form-sizing: content; // Autoresize
        min-height: 2lh;
        max-height: 10lh;
    }

    :where(input),
    :where(textarea) {
        background: var(--brand__input--bg);
        padding: var(--brand__input--padding);
        margin: 0;
        border: var(--brand__input--border);
        border-radius: var(--brand__input--border-radius);
        box-shadow: none;
        font-size: 1rem;
        max-width: 100%;

        &:disabled {
            background: var(--brand__color--light-gray);
        }
    }

    :where([type="radio"], [type="checkbox"]) {
        margin: 0;
        padding: 0;
        height: auto;
        border: none;
        box-shadow: none;
    }

    :where(fieldset) {
        margin: 0;
        padding: 0;
        border: none;
    }

    :where(legend) {
        padding: 0;
    }
}
