@layer fonts {
    // Self host your fonts.
    // Download Google Fonts at https://gwfh.mranftl.com/fonts

    @font-face {
        font-family: "Quicksand";

        // Path from root or relative to your entry file
        src: url("@root/Module/Projects/EpiBase/WebEssentials/Mvc/_Shared/fonts/quicksand-regular.woff2")
            format("woff2");
        font-weight: 400;
        font-style: normal;
        font-display: swap;
    }

    @font-face {
        font-family: "Quicksand";

        // Path from root or relative to your entry file
        src: url("@root/Module/Projects/EpiBase/WebEssentials/Mvc/_Shared/fonts/quicksand-700.woff2") format("woff2");
        font-weight: 700;
        font-style: normal;
        font-display: swap;
    }
}
