@layer components {
    we-faq {
        display: block;

        & > h2 {
            margin: none;
        }

        &[boxed] {
            padding-inline: var(--wrapper--mimic-spacing);
        }

        details {
            background-color: var(--brand__color--gray-lighter);
            margin-bottom: var(--brand__spacing);
        }

        summary {
            display: flex;
            justify-content: space-between;
            align-items: baseline;
            cursor: pointer;
            padding: var(--brand__spacing);
            gap: var(--brand__spacing);

            // Fake opening/closing animation by transitioning margin
            transition: margin 0.2s ease-out;

            h3 {
                margin: 0;
            }

            svg {
                --svg-size: 20px;
                transition: transform 0.25s ease-in-out;
                flex-shrink: 0;
            }
        }

        details[open] summary {
            margin-block-end: 10px; // For fake animation

            svg {
                transform: rotate(180deg);
            }
        }

        .c-faq-item__body {
            padding: 0 var(--brand__spacing) var(--brand__spacing) var(--brand__spacing);
        }
    }
}
