@layer components {
    we-component {
        margin-block-end: var(--component-spacing);
        display: block;

        &[spacing="default"] {
            --component-spacing: var(--brand__spacing);
        }

        &[spacing="small"] {
            --component-spacing: var(--brand__spacing--large);
        }

        &[spacing="large"] {
            --component-spacing: var(--brand__spacing--large);
        }

        &[spacing="huge"] {
            --component-spacing: var(--brand__spacing--huge);
        }
    }
}
