@layer utilities {
    .u-color-primary {
        color: var(--brand__color--primary);
    }

    .u-color-primary-light {
        color: var(--brand__color--primary-light);
    }

    .u-color-primary-lighter {
        color: var(--brand__color--primary-lighter);
    }

    .u-color-secondary {
        color: var(--brand__color--secondary);
    }

    .u-color-secondary-light {
        color: var(--brand__color--secondary-light);
    }

    .u-color-secondary-lighter {
        color: var(--brand__color--secondary-lighter);
    }

    .u-color-white {
        color: #fff;
    }

    .u-bg-color-primary,
    .u-bg-color-secondary,
    .u-bg-color-gray-light,
    .u-bg-color-accent1 {
        --bg-color-active: 1;
    }

    // Review: Not in lign with Figma
    .u-bg-color-primary,
    .u-bg-color-secondary {
        color: var(--brand__color--white);

        --quote--color: var(--brand__color--white);

        --brand__button-primary--bg: var(--brand__color--white);
        --brand__button-primary--color: var(--brand__color--black);

        --brand__link--color: var(--brand__color--white);

        --set-brand__heading--color: currentColor;
    }

    .u-bg-color-primary {
        background-color: var(--brand__color--primary);

        --btn-primary--bc: white;
        --btn-secondary--bc: white;
        --btn-secondary--color: var(--brand__color--text);
    }

    .u-bg-color-secondary {
        background-color: var(--brand__color--secondary);

        --btn-primary--bc: white;
        --btn-primary--bg: transparent;
        --btn-secondary--bc: white;
        --btn-secondary--color: var(--brand__color--text);

        --quote--color: var(--brand__color--white);
    }

    .u-bg-color-gray {
        background-color: var(--brand__color--gray);
    }

    .u-bg-color-gray-light {
        background-color: var(--brand__color--gray-light);
    }

    .u-bg-color-white {
        background-color: var(--brand__color--white);
    }

    .u-bg-color-accent1 {
        color: var(--brand__color--accent1);
        background-color: var(--brand__color--accent1--bg);
    }
}
