@layer components {
    we-footer {
        display: block;
        container-type: inline-size;

        footer {
            background-color: var(--brand__color--gray-lighter);
        }

        h2,
        h3 {
            font-size: var(--brand__font-size--20px);
        }

        .we-footer__container {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: var(--brand__spacing--large) var(--brand__spacing--huge);
            padding: var(--brand__spacing) 0;
        }

        .we-footer__info,
        .we-footer__connect {
            align-self: start;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: var(--brand__spacing--small) var(--brand__spacing--large);
        }

        .we-footer__company {
            display: flex;
            align-items: flex-start;
            align-self: start;
            flex-wrap: wrap;
            gap: var(--brand__spacing) var(--brand__spacing--large);

            img {
                width: auto;
                max-height: 50px;
            }
        }

        .we-footer__company-info {
            max-width: 50ch;
        }

        .we-footer__media {
            li {
                display: inline-block;
                border-radius: 50px;
                background-color: var(--brand__color--gray);
                padding: var(--brand__spacing--small);
            }
        }

        .we-footer__links {
            display: flex;
            flex-wrap: wrap;
            gap: clamp(20px, 5vw, 50px);

            & > * {
                flex: 1 1 0;
            }

            ul {
                display: flex;
                flex-direction: column;
                gap: var(--brand__spacing--small);
                margin-block-end: var(--brand__spacing);
            }
        }

        .we-footer__end {
            background-color: var(--brand__color--primary);

            &,
            & a {
                color: #fff;
            }
        }

        .we-footer__terms {
            padding: var(--brand__spacing--small) 0;
            font-size: var(--brand__font-size--small);
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .we-footer__terms,
        .we-footer__terms ul {
            display: flex;
            gap: var(--brand__spacing--tiny) var(--brand__spacing);
        }

        @container (width < 1000px) {
            font-size: var(--brand__font-size--small);

            .we-footer__container {
                grid-template-columns: 1fr;
            }

            .we-footer__info,
            .we-footer__connect {
                justify-content: space-between;
            }
        }
    }
}
