@layer settings {
    :root {
        --scroll-behavior: smooth;

        @media screen and (prefers-reduced-motion: reduce) {
            --scroll-behavior: auto;
        }
    }
}

@layer elements {
    html {
        scroll-behavior: var(--scroll-behavior);
    }

    body {
        color: var(--brand__color--text);
        // TODO align with figma and impl in base
        background-color: var(--brand__color--site-bg);
        font-family: var(--brand__font-family);
        font-size: 1rem;
        accent-color: var(--brand__color--primary);
    }

    // Targets elements that are scroll into view via eg. anchor tags. Takes into account height of header
    :target {
        scroll-margin-top: var(--set-scroll-margin-top, calc(var(--header--height, 0) + var(--brand__spacing)));
    }

    /* :focus-visible {
        outline: 2px dotted var(--brand__color--focus);
        outline-offset: 2px;
    } */

    :focus-visible {
        outline: none;
    }

    :where(summary) {
        cursor: pointer;
        list-style: none;
        user-select: none;

        &::-webkit-details-marker {
            display: none;
        }
    }

    :where(strong) {
        font-weight: var(--brand__font-weight--bold, bold);
    }

    :where(figure, figcaption, blockquote) {
        padding: 0;
        margin: 0;
    }

    :where(p) {
        margin: 0 0 1.5em;
    }

    :where(dt) {
        font-weight: var(--brand__font-weight--bold);
    }

    // fix: hide doormat when script is not loaded
    dlw-reveal {
        & > [slot="content"] {
            display: none;
        }

        &[open] > [slot="content"] {
            display: block;
        }
    }

    address {
        font-style: normal;
    }
}
