@layer elements {
    a {
        color: var(--brand__link--color, currentColor);
        transition: var(--brand__link--transition, color 0.2s ease);
        text-decoration: var(--brand__link--text-decoration, underline);
        // text-decoration-style: solid;
        // text-decoration-thickness: var(--brand__link--text-decoration-thickness);
        text-decoration-color: currentColor;
        text-underline-offset: 0.25em;
        will-change: color;

        &:active,
        &:hover {
            cursor: pointer;
            color: var(--brand__link--hover--color, var(--brand__link--color));
        }

        &:focus {
            color: var(--brand__link--focus--color, var(--brand__link--hover--color, var(--brand__link--color)));
        }
    }
}
