@layer utilities {
    .u-txt-tiny {
        font-size: var(--brand__font-size--12px);
    }

    .u-txt-small {
        font-size: var(--brand__font-size--small);
    }

    .u-txt-large {
        font-size: var(--brand__font-size--large);
    }

    .u-txt-huge {
        font-size: var(--brand__font-size--20px);
    }

    .u-txt-center {
        text-align: center;
    }

    .u-txt-end {
        text-align: end;
    }

    .u-txt-start {
        text-align: start;
    }

    .u-txt-upper {
        text-transform: uppercase;
    }

    .u-txt-bold {
        font-weight: var(--brand__font-weight--bold);
    }

    .u-set-font-size-px {
        font-size: calc(var(--v, 16px) / 16px);
    }
}
