@use "../tools/tools.position" as *;
@forward "../objects/objects.card";
@forward "../objects/objects.flex";
@forward "../objects/objects.gap";
@forward "../atoms/atoms.badge";
@forward "../elements/elements.headings";

@layer components {
    .c-card {
        background: var(--brand__color--bg);

        &__media {
            img,
            video,
            picture {
                aspect-ratio: 4/3;
            }
        }

        &__content {
            background-color: var(--brand__color--gray-lighter);
            padding: var(--brand__spacing);
        }

        &__heading {
            display: flex;
            align-items: baseline;
            justify-content: space-between;
            gap: 0 var(--brand__spacing--small);
        }

        &__title {
            margin: 0;
        }

        &__body {
            padding: var(--brand__spacing--small) var(--brand__spacing) var(--brand__spacing);
        }

        time {
            color: var(--brand__color--secondary);
            font-size: var(--brand__font-size--small);
            font-weight: var(--brand__font-weight--bold);
        }

        &__badge {
            grid-area: media;
            align-self: start;
            z-index: 1;
        }

        // For this to work, the parent of a card must be of type o-grid-item or should contain container-type: inline-size is set
        @container (width < 400px) {
            &__heading {
                flex-direction: column;
            }
        }
    }
}
