@layer atoms {
  .a-btn {
    position: relative;
    display: inline-flex;
    align-items: center;
    gap: var(--brand__spacing--tiny);
    justify-content: center;
    background: var(--btn--bgc, transparent);
    color: var(--btn--color, currentColor);
    border: 2px solid var(--btn--bc, transparent);
    text-decoration: none;
    text-transform: uppercase;
    padding: var(--btn--padding);
    transition: var(--btn--transition, color 0.3 ease-in-out);
    cursor: pointer;
    flex-shrink: 0;
    z-index: var(--btn--zindex);
  }
  .a-btn:disabled, .a-btn[aria-disabled=true], .a-btn[disabled] {
    pointer-events: none;
    opacity: 0.5;
  }
  .a-btn--secondary,
  .a-btn--primary {
    --btn--padding: 16px 25px;
  }
  .a-btn--secondary:hover, .a-btn--secondary:focus,
  .a-btn--primary:hover,
  .a-btn--primary:focus {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  }
  .a-btn--primary {
    border-color: var(--btn-primary--bc, var(--brand__color--primary));
    background-color: var(--btn-primary--bg, var(--brand__color--primary));
    color: var(--btn-primary--color, var(--brand__color--white));
  }
  .a-btn--secondary {
    border-color: var(--btn-secondary--bc, currentColor);
    background-color: var(--btn-secondary--bg, var(--brand__color--white));
    color: var(--btn-secondary--color, currentColor);
  }
  .a-btn-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: var(--btn--wrapper--jc);
    gap: var(--brand__spacing--small) var(--brand__spacing--small);
  }
}
:root {
  --loading-size: 48px;
  --loading--top: 20%;
}

@supports selector(:has(.some-element)) {
  *:has(> dlw-loader[overlay]) {
    min-height: 200px;
  }
}
dlw-loader {
  display: inline-flex;
  align-items: center;
  gap: var(--brand__spacing--small);
}
dlw-loader[overlay] {
  --loading-after--color: var(--brand__color--secondary);
  --loading--color: white;
  --svg-size: 2rem;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: var(--brand__z-index--loading);
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  color: white;
}
dlw-loader[small] {
  --loading-size: 1rem;
}

[aria-busy=false] > dlw-loader {
  display: none;
}

.a-loading-wrapper {
  display: inline-flex;
  align-items: center;
  gap: var(--brand__spacing--small);
}

.a-loading {
  transform: rotateZ(45deg);
  perspective: 1000px;
  border-radius: 50%;
  width: var(--loading-size);
  height: var(--loading-size);
  color: var(--loading--color, var(--brand__color--secondary));
  flex-shrink: 0;
}
.a-loading::before, .a-loading:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: inherit;
  height: inherit;
  border-radius: 50%;
  transform: rotateX(70deg);
  animation: 1s spin linear infinite;
}
.a-loading::after {
  color: var(--loading-after--color, var(--brand__color--primary));
  transform: rotateY(70deg);
  animation-delay: 0.4s;
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%) rotateZ(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotateZ(360deg);
  }
}
@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(-360deg);
  }
}
@keyframes spin {
  0%, 100% {
    box-shadow: 0.2em 0px 0 0px currentcolor;
  }
  12% {
    box-shadow: 0.2em 0.2em 0 0 currentcolor;
  }
  25% {
    box-shadow: 0 0.2em 0 0px currentcolor;
  }
  37% {
    box-shadow: -0.2em 0.2em 0 0 currentcolor;
  }
  50% {
    box-shadow: -0.2em 0 0 0 currentcolor;
  }
  62% {
    box-shadow: -0.2em -0.2em 0 0 currentcolor;
  }
  75% {
    box-shadow: 0px -0.2em 0 0 currentcolor;
  }
  87% {
    box-shadow: 0.2em -0.2em 0 0 currentcolor;
  }
}
