@layer components {
    .c-doormat {
        cursor: default;
        padding: 0;
        position: absolute;
        top: var(--primary-nav--height-full-screen);
        left: 0;
        z-index: 2;
        width: 100%;
        height: calc(100% - calc(var(--primary-nav--height-full-screen) + (var(--brand__spacing) * 2)));
        padding: var(--brand__spacing);
        background-color: var(--brand__color--gray-light);
    }

    .c-doormat--primary {
        top: calc(var(--primary-nav--height-full-screen) + (var(--brand__spacing) * 2));
    }

    .c-doormat--language {
        position: initial;
    }

    .c-doormat--cta {
        padding: var(--brand__spacing);
        position: absolute;
        left: 0;
        height: auto;

        search form {
            display: flex;
            flex-direction: column;
            gap: var(--brand__spacing--small);

            button {
                align-self: self-start;
            }
        }
    }

    .c-doormat-wrapper {
        height: 100%;
        display: grid;
        grid-template-rows: auto 1fr;

        > button {
            background-color: var(--brand__color--primary);
            border: 0;
            border-radius: 0;
            margin-block-end: var(--brand__spacing);
            padding: var(--brand__spacing--small);
            justify-content: flex-start;
            align-items: center;
            text-align: start;
            font-weight: 700;
            gap: var(--brand__spacing--small);
            color: var(--brand__color--white);

            a {
                text-decoration: none;
            }
        }
    }

    @media (--until-desktop) {
        dlw-reveal[open] {
            .c-doormat-wrapper > button {
                display: flex !important; //override u-hide display block
            }
        }
    }

    @media (--desktop) {
        .c-doormat {
            background-color: var(--content--background-color, var(--brand__color--gray-lighter));
            color: var(--brand__color--text);
            height: auto;
            position: absolute;
            left: auto;
            top: auto;
            width: auto;
            --set-brand__link--color: var(--brand__color--text);

            a,
            span {
                color: var(--brand__color--text);
            }
        }

        .c-doormat--language {
            --content--width: 100%;
        }

        .c-doormat--large {
            left: 0;
            width: 100vw;
            padding-inline: var(--wrapper--mimic-spacing);
            padding-block: var(--brand__spacing);
        }

        .c-doormat--cta {
            padding: var(--brand__spacing--large);
            left: unset;
            right: 0;
            min-width: 500px;
            --content--width: fit-content;
        }
    }

    .c-doormat-items {
        padding: 0;
        overflow-y: auto;
    }

    .c-doormat-item {
        cursor: pointer;
        margin-block: 0 var(--brand__spacing--small);
        list-style-type: none;
        width: fit-content;
    }
}
