$spacings: (
    "none": 0px,
    "tiny": var(--brand__spacing--tiny),
    "small": var(--brand__spacing--small),
    null: var(--brand__spacing),
    "large": var(--brand__spacing--large),
    "huge": var(--brand__spacing--huge),
) !default;

@layer utilities {
    .u-p {
        @each $key, $value in $spacings {
            // Padding
            &#{if($key != null, "-", "")}#{$key} {
                padding: #{$value};
            }

            // Beware: generates a lot of classes. Turn on when necessary.

            // Padding inline start:
            // &is#{if($key != null, "-", "")}#{$key} {
            //     padding-inline-start: #{$value};
            // }

            // Padding block start:
            // &bs#{if($key != null, "-", "")}#{$key} {
            //     padding-block-start: #{$value};
            // }

            // Padding inline end:
            // &ie#{if($key != null, "-", "")}#{$key} {
            //     padding-inline-end: #{$value};
            // }

            // Padding block end:
            // &be#{if($key != null, "-", "")}#{$key} {
            //     padding-block-end: #{$value};
            // }

            // Inline padding (x)
            &i#{if($key != null, "-", "")}#{$key} {
                padding-inline: #{$value};
            }

            // Block padding (y)
            &b#{if($key != null, "-", "")}#{$key} {
                padding-block: #{$value};
            }
        }
    }

    .u-m {
        @each $key, $value in $spacings {
            &#{if($key != null, "-", "")}#{$key} {
                margin: #{$value};
            }

            // Beware: generates a lot of classes. Turn on when necessary.

            // Margin inline start:
            // &is#{if($key != null, "-", "")}#{$key} {
            //     margin-inline-start: #{$value};
            // }

            // Margin block start:
            // &bs#{if($key != null, "-", "")}#{$key} {
            //     margin-block-start: #{$value};
            // }

            // Margin inline end:
            // &ie#{if($key != null, "-", "")}#{$key} {
            //     margin-inline-end: #{$value};
            // }

            // Margin block end:
            // &be#{if($key != null, "-", "")}#{$key} {
            //     margin-block-end: #{$value};
            // }

            // Inline margin (x)
            &i#{if($key != null, "-", "")}#{$key} {
                margin-inline: #{$value};
            }

            // Block margin (y)
            &b#{if($key != null, "-", "")}#{$key} {
                margin-block: #{$value};
            }
        }
    }

    // Requires flexbox parent
    .u-push-inline-end {
        margin-inline-start: auto;
    }

    .u-push-block-end {
        margin-block-start: auto;
    }
}
