@use "@css-shared/tools/tools.fonts" as *;

:root {
    --brand__font--primary: Quicksand;
    --brand__font--fallback: Arial, sans-serif;
    --brand__font-family: var(--brand__font--primary), var(--brand__font--fallback);

    --brand__font-weight--regular: 400;
    --brand__font-weight--bold: 700;

    --brand__font-size--tiny: #{rem(12px)};
    --brand__font-size--small: #{rem(14px)};
    --brand__font-size--large: #{rem(18px)};
    --brand__font-size--huge: #{rem(20px)};

    --brand__font-size--12px: #{rem(12px)};
    --brand__font-size--14px: #{rem(14px)};
    --brand__font-size--18px: #{rem(18px)};
    --brand__font-size--20px: #{rem(20px)};

    --brand__heading--font-weight: var(--brand__font-weight--bold);
    --brand__font-weight--h4: var(--brand__font-weight--bold);

    // https://modern-fluid-typography.vercel.app/
    --brand__font-size--h1: clamp(1.875rem, 2vw + 1rem, 2.875rem);
    --brand__font-size--h2: clamp(1.625rem, 2vw + 1rem, 2.188rem);
    --brand__font-size--h3: #{rem(25px)};
    --brand__font-size--h4: #{rem(22px)};
    --brand__font-size--h5: #{rem(20px)};
    --brand__font-size--h6: #{rem(18px)};
}
