@layer components {
    dlw-lightgallery {
        &[boxed] {
            margin-inline: var(--wrapper--mimic-spacing);
        }
    }

    [dlw-lightgallery-child] {
        cursor: pointer;
        background-blend-mode: var(--lightgallery-child--bbm);
        border-width: 0;
        padding: 0;
        background-color: transparent;

        &:focus-visible {
            outline: none;
            box-shadow: var(--lightgallery-child-focus--box-shadow, 0px 12px 12px 0px rgb(0 0 0 / 40%));
        }

        &:is([disabled], [aria-disabled="true"]) {
            pointer-events: none;
            opacity: 0.5;
            filter: grayscale(1);
        }
    }

    .lg-custom-thumbnails {
        .lg-outer {
            width: auto;
            right: 125px;
            left: 10px;

            &.lg-components-open {
                --lg-thumb-outer--opacity: 1;
                --lg-thumb-outer--transform: translate3d(0, 0, 0);
            }

            .lg-thumb-outer {
                scrollbar-gutter: stable;
                left: auto;
                top: 0;
                width: 125px;
                bottom: 0;
                position: fixed;
                right: 0;
                max-height: none;
                background-color: rgba(255, 255, 255, 0.2);
                padding-left: 5px;
                padding-right: 5px;
                margin: 0 -10px;
                overflow-y: auto;

                // Update transition values
                // By default thumbnails animates from bottom to top
                // Change that from right to left.
                will-change: transform opacity;
                opacity: var(--lg-thumb-outer--opacity, 0);
                transition: transform 0.15s cubic-bezier(0, 0, 0.25, 1) 0s, cubic-bezier(0, 0, 0.25, 1) 0.15s;
                transform: var(--lg-thumb-outer--transform, 0);
            }

            .lg-thumb-item {
                filter: grayscale(100%);
                will-change: filter;
                transition: filter 0.12s ease-in, border-color 0.12s ease;
                float: none;
                border-color: #545454;

                &:hover,
                &.active {
                    filter: grayscale(0);
                    border-color: #fff;
                }
            }

            .lg-thumb {
                max-width: 100%;
                padding: 5px 0;
            }
        }
    }
}
