@layer components {
    we-rtm {
        display: block;
        container-type: inline-size;

        &[direction="inversed"] {
            .we-rtm__body-wrapper {
                order: 2;
            }

            .we-rtm__body {
                padding-block-start: 0;
            }
        }

        &:not([direction="inversed"]) {
            .we-rtm__body {
                padding-block-end: 0;
            }
        }

        & > section {
            display: grid;
            grid-template-columns: 1fr;
            gap: var(--brand__spacing) clamp(var(--brand__spacing), 5vw, var(--brand__spacing--huge));
        }

        .we-rtm__body {
            display: flex;
            flex-direction: column;
            gap: var(--brand__spacing);
            padding: var(--wrapper--spacing, 0px);
        }

        .we-rtm__link {
            display: block;
        }

        @container (width > 1000px) {
            &[direction="inversed"] {
                .we-rtm__body-wrapper {
                    padding-inline: 0 var(--wrapper--mimic-spacing);
                }
            }

            &[boxed] {
                .we-rtm__obj-wrapper {
                    padding-inline: 0 var(--wrapper--mimic-spacing);
                }
            }

            &[boxed][direction="inversed"] {
                .we-rtm__obj-wrapper {
                    padding-inline: var(--wrapper--mimic-spacing) 0;
                }
            }

            & > section {
                grid-template-columns: repeat(2, 1fr);
                align-items: center;
                padding-block: calc(var(--bg-color-active) * var(--brand__spacing--large, 0px));
            }

            .we-rtm__body-wrapper {
                padding-inline: var(--wrapper--mimic-spacing) 0;
                align-self: center;
            }

            .we-rtm__body {
                padding: 0;
            }
        }
    }
}
