@layer components {
    .c-submenu {
        position: absolute;
        display: flex;
        box-shadow: 0 0 11px rgb(33 33 33 / 20%);

        &__list {
            list-style: none;
            padding: 40px;
            margin: 0;
        }

        &__item {
            padding-bottom: var(--brand__spacing--small);
        }

        &__btn a {
            background-color: var(--brand__color--black);
            color: var(--brand__color--white);
        }

        &__highlights {
            max-width: min-content;
            padding: 40px;
            background-color: #f2f5f8;
        }
    }
}
