@use "@css-shared/tools/tools.fonts" as *;

// Defaults for now? To review (both naming and values)
$brand__breakpoint--xs: 360px !default;
$brand__breakpoint--mobile: 576px !default; // 36em
$brand__breakpoint--tablet: 740px !default; // 46.25em
$brand__breakpoint--desktop: 980px !default; // 61.25em
$brand__breakpoint--large: 1260px !default; // 78.75em
$brand__breakpoint--wide: 1400px !default; // 87.5em

// Custom media queries (transpiled by PostCSS)
// https://github.com/csstools/postcss-plugins/tree/main/plugins/postcss-custom-media
@custom-media --mobile (min-width: #{rem($brand__breakpoint--mobile)});
@custom-media --tablet (min-width: #{rem($brand__breakpoint--tablet)});
@custom-media --desktop (min-width: #{rem($brand__breakpoint--desktop)});
@custom-media --large (min-width: #{rem($brand__breakpoint--large)});
@custom-media --wide (min-width: #{rem($brand__breakpoint--wide)});
@custom-media --until-xs (max-width: #{rem($brand__breakpoint--xs - 1)});
@custom-media --until-mobile (max-width: #{rem($brand__breakpoint--mobile - 1)});
@custom-media --until-tablet (max-width: #{rem($brand__breakpoint--tablet - 1)});
@custom-media --until-desktop (max-width: #{rem($brand__breakpoint--desktop - 1)});
@custom-media --until-large (max-width: #{rem($brand__breakpoint--large - 1)});
@custom-media --until-wide (max-width: #{rem($brand__breakpoint--wide - 1)});

// These are variables that can be used to pass to web components watching for changes in viewport
// (eg. using window.matchMedia: dlw-reveal-utils, ...)
:root {
    --mq-desktop: (min-width: #{rem($brand__breakpoint--desktop)});
    --mq-until-desktop: (max-width: #{rem($brand__breakpoint--desktop - 1)});
}
