:root {
    --brand__color--primary: #333333;
    --brand__color--primary-light: #ba86d0;
    --brand__color--primary-lighter: #e4cfec;

    --brand__color--secondary: #85c2a5;
    --brand__color--secondary-light: #97ccd0;
    --brand__color--secondary-lighter: #d5eaec;

    --brand__color--focus: var(--brand__color--secondary);
    --brand__color--link: #ba86d0;
    --brand__color--bg: #edf1f3;
    // TODO align with figma and impl in base
    --brand__color__site__bg: var(--brand__color--white);

    --brand__color--text: #424242;
    --brand__color--text-light: #666666;

    --brand__color--border: #cfcfcf;

    --brand__color--black: #000000;
    --brand__color--white: #ffffff;
    --brand__color--gray: #2c2a29;
    --brand__color--gray-light: #84888b;
    --brand__color--gray-lighter: #e8e7e8;

    // Accent colors
    --brand__color--accent1: #34495e;
    --brand__color--accent1--bg: #f2f5f8;
}
