@layer components {
    we-usp {
        container-type: inline-size;

        &[boxed] {
            display: block;
            padding-inline: var(--wrapper--mimic-spacing);
        }

        &[align="left"] {
            .c-usp-item {
                align-items: start;
            }
        }

        &[align="center"] {
            .c-usp-item {
                align-items: center;
            }
        }

        &[align="right"] {
            .c-usp-item {
                align-items: end;
            }
        }

        .c-usp-list {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: var(--brand__spacing);
            padding: 0;
            list-style: none;

            @container (width > 60ch) {
                grid-template-columns: repeat(4, 1fr);
            }
        }

        .c-usp-item {
            display: flex;
            flex-direction: column;
            padding: var(--brand__spacing);

            h3 {
                font-size: var(--brand__font-size--h1);
            }

            h3,
            p {
                margin-block-end: 0;
            }

            picture {
                max-width: max-content;
                margin-block: 0 var(--brand__spacing);
            }

            img {
                width: auto;
            }
        }
    }
}
