@layer atoms {
    .a-skip-link {
        width: 100%;
        position: fixed;
        top: -2000px;
        color: #fff;
        background: var(--brand__color--secondary);
        padding: 0.5rem 1rem;
        display: inline-block;
        box-shadow: 0 0 25px rgba(0, 0, 0, 0.5);
        text-align: center;
        margin: 0;
        z-index: 1000;

        &:focus-visible {
            top: 0;
            left: 0;
        }

        a {
            text-decoration: none;
        }
    }
}
