@use "../tools/tools.position" as tools;
// change loading icon with other css see examples: https://cssloaders.github.io/
:root {
    --loading-size: 48px;
    --loading--top: 20%;
}

@supports selector(:has(.some-element)) {
    *:has(> dlw-loader[overlay]) {
        min-height: 200px;
    }
}

dlw-loader {
    display: inline-flex;
    align-items: center;
    gap: var(--brand__spacing--small);

    &[overlay] {
        --loading-after--color: var(--brand__color--secondary);
        --loading--color: white;
        --svg-size: 2rem;
        @include tools.absolute(0, 0, 0, 0, var(--brand__z-index--loading));
        justify-content: center;
        background: rgba(0, 0, 0, 0.5);
        color: white;
    }

    &[small] {
        --loading-size: 1rem;
    }
}

[aria-busy="false"] > dlw-loader {
    display: none;
}

.a-loading-wrapper {
    display: inline-flex;
    align-items: center;
    gap: var(--brand__spacing--small);
}

.a-loading {
    transform: rotateZ(45deg);
    perspective: 1000px;
    border-radius: 50%;
    width: var(--loading-size);
    height: var(--loading-size);
    color: var(--loading--color, var(--brand__color--secondary));
    flex-shrink: 0;

    &::before,
    &:after {
        content: "";
        display: block;
        @include tools.absolute(0, null, null, 0);
        width: inherit;
        height: inherit;
        border-radius: 50%;
        transform: rotateX(70deg);
        animation: 1s spin linear infinite;
    }

    &::after {
        color: var(--loading-after--color, var(--brand__color--primary));
        transform: rotateY(70deg);
        animation-delay: 0.4s;
    }
}

@keyframes rotate {
    0% {
        transform: translate(-50%, -50%) rotateZ(0deg);
    }
    100% {
        transform: translate(-50%, -50%) rotateZ(360deg);
    }
}

@keyframes rotateccw {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, -50%) rotate(-360deg);
    }
}

@keyframes spin {
    0%,
    100% {
        box-shadow: 0.2em 0px 0 0px currentcolor;
    }
    12% {
        box-shadow: 0.2em 0.2em 0 0 currentcolor;
    }
    25% {
        box-shadow: 0 0.2em 0 0px currentcolor;
    }
    37% {
        box-shadow: -0.2em 0.2em 0 0 currentcolor;
    }
    50% {
        box-shadow: -0.2em 0 0 0 currentcolor;
    }
    62% {
        box-shadow: -0.2em -0.2em 0 0 currentcolor;
    }
    75% {
        box-shadow: 0px -0.2em 0 0 currentcolor;
    }
    87% {
        box-shadow: 0.2em -0.2em 0 0 currentcolor;
    }
}
