@layer utilities {
    .u-hide-until-desktop {
        display: none;

        @media (--desktop) {
            display: block;
        }
    }

    .u-hide-desktop {
        display: block;

        @media (--desktop) {
            display: none;
        }
    }

    .u-hidden {
        display: none;
    }

    .u-block {
        display: block;
    }
}
