@layer components {
    .c-nav {
        position: var(--nav--position, initial);
        display: var(--nav--display, flex);
        justify-content: var(--nav--justify-content, space-between);
        height: var(--nav--height, auto);

        @media (--desktop) {
            --nav--justify-content: flex-end;
        }
    }

    .c-nav--primary {
        font-size: var(--brand__font-size);
        --nav__items--desktop--position: static;
        --nav__items--width: 100%;
        --nav__item--height: var(--primary-nav--height);
        --nav__button--height: var(--primary-nav--height);
        --nav__button--desktop--height: var(--primary-nav--height-full-screen);
        --nav__item--desktop--height: var(--primary-nav--height-full-screen);

        @media (--until-desktop) {
            margin-block: 0 var(--brand__spacing);
        }
    }

    .c-nav--secondary {
        font-size: var(--brand__font-size--small);
        --nav--height: var(--secondary-nav--height);
        --nav__item--height: var(--secondary-nav--height);
        --nav__button--height: var(--secondary-nav--height);
        --nav__button--desktop--height: var(--secondary-nav--height-full-screen);
        --nav__item--desktop--height: var(--secondary-nav--height-full-screen);
        @media (--desktop) {
            --nav__items--padding: 0 var(--brand__spacing) 0 0;
        }
    }

    .c-nav--language {
        --nav__item--desktop--position: relative;
        --nav--justify-content: space-around;
        @media (--desktop) {
            --nav--justify-content: flex-end;
        }
        --nav__item--height: var(--secondary-nav--height);
        --nav__button--height: var(--secondary-nav--height);
        --nav__button--desktop--height: var(--secondary-nav--height-full-screen);
        --nav__item--desktop--height: var(--secondary-nav--height-full-screen);
    }

    .c-nav--cta {
        --nav__item--justify-content: center;
        --nav__item--height: var(--primary-nav--height);
        --nav__item--width: var(--primary-nav--height-full-screen);
        --nav__item--desktop--height: var(--primary-nav--height-full-screen);
        --set-brand__link--color: var(--band__color--white);
        --nav__item--color: var(--band__color--white);

        .c-nav__item {
            padding-inline: var(--brand__spacing--small);
        }
    }

    .c-nav--mobile-cta {
        --nav__item--justify-content: center;
        --nav__item--height: var(--primary-nav--height-full-screen);
        --nav__item--width: var(--primary-nav--height-full-screen);
        --nav__item--desktop--height: var(--primary-nav--height-full-screen);
        --set-brand__link--color: black;
        --nav__item--color: black;

        @media (--desktop) {
            --nav--display: none;
        }
    }

    .c-nav__items {
        width: var(--nav__items--width, initial);
        column-gap: var(--brand__spacing);
        row-gap: var(--brand__spacing);
        margin: 0;
        padding: var(--nav__items--padding, 0 var(--brand__spacing));
        display: flex;
        flex-direction: column;
        list-style-type: none;
        display: flex;
        align-items: flex-start;
        justify-content: var(--nav__item--justify-content, space-between);
        box-sizing: border-box;

        @media (--desktop) {
            position: var(--nav__items--desktop--position, initial);
            flex-direction: row;
            width: auto;
            justify-content: center;
        }
    }

    .c-nav__item {
        cursor: pointer;
        transition: color 0.2s ease;
        text-align: center;
        gap: 10px;
        border: none;
        font-weight: 400;
        //font-size: var(--brand__font-size);
        color: currentColor;
        background-color: transparent;
        padding: 0;
        display: flex;
        height: var(--nav__item--height, initial);
        width: var(--nav__item--width, 100%);
        color: var(--nav__item--color, inherit);
        align-items: center;
        justify-content: var(--nav__item--justify-content, space-between);
        box-sizing: border-box;

        a,
        button {
            flex-shrink: 0;
        }

        @media (--until-desktop) {
            dlw-reveal,
            .c-nav__button {
                width: 100%;
                --nav__item--width: 100%;
            }
        }

        @media (--desktop) {
            padding: 0;
            height: var(--nav__item--desktop--height, initial);
            justify-content: center;
            position: var(--nav__item--desktop--position, initial);
            --nav__item--width: auto;
        }

        &:hover {
            color: gray;
            --set-brand__link--color: gray;
        }
    }

    .c-nav__button {
        cursor: pointer;
        height: var(--nav__button--height, initial);
        transition: color 0.2s ease;
        text-align: center;
        gap: 10px;
        border: none;
        font-weight: 400;
        //font-size: var(--brand__font-size);
        color: currentColor;
        background-color: transparent;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (--until-desktop) {
            svg {
                transform: rotate(-90deg);
            }
        }

        @media (--desktop) {
            height: var(--nav__button--desktop--height, initial);
        }

        svg {
            width: 8px;
        }
    }
}
