@use "../tools/tools" as *;

// Some ideas from https://inclusive-components.design/cards/ are used
// Heading should go first, so we change the order of the image with flex.

@layer objects {
    .o-card {
        position: relative;
        display: grid;
        grid-template-rows: auto 1fr;
        grid-template-areas: "media" "body";
        height: 100%;
    }

    .o-card__media {
        grid-area: media;
        position: relative;

        img,
        video,
        picture {
            width: 100%;
        }
    }

    .o-card__body {
        grid-area: body;
        display: flex;
        flex-direction: column;
    }

    // Part of body, pushed to bottom
    .o-card__footer {
        margin: auto 0 0;
    }

    // Used for links within a hx tag, to enable fully clickable card with useful accessible label
    // See https://inclusive-components.design/cards/
    .o-card__overlay {
        @include before {
            @include absolute(0, 0, 0, 0, 1);
        }
    }
}
