@layer atoms {
    .a-rt {
        :where(ol),
        :where(ul),
        :where(p) {
            margin-block-end: var(--brand__spacing);

            &:last-child {
                margin-block-end: 0;
            }
        }

        :where(a) {
            text-decoration: underline;
        }

        &[class*="u-bg-color-"] {
            padding: var(--brand__spacing);
        }
    }
}
