@layer objects {
    .o-toggle[aria-expanded="true"] {
        .o-toggle__initial-open {
            display: none;
        }

        .o-toggle__initial-close {
            display: block;
        }
    }

    .o-toggle[aria-expanded="false"] {
        .o-toggle__initial-open {
            display: block;
        }

        .o-toggle__initial-close {
            display: none;
        }
    }
}
